import styled from '@xstyled/styled-components'
import { GatsbySeo } from 'gatsby-plugin-next-seo'
import * as React from 'react'

import Layout from '../components/Layout'
import Section from '../containers/Section'
import { contentRenderer } from '../utils/renderUtils'

const Page = styled.main`
  max-width: 1100;
  margin: 0 auto;
`

const Content = styled.div`
  max-width: 721;
  margin: 4rem auto;
  display: flex;
  flex-flow: column nowrap;
`

const IssueTemplate = ({ pageContext }) => {
  const { campaign, hero, sections, images, meta } = pageContext

  const { title, description, image } = meta

  const ogImage = images.allFile.edges.find(
    ({ node }) => node.relativePath === image
  )

  return (
    <Layout>
      <GatsbySeo
        title={title}
        description={description}
        openGraph={{
          title,
          description,
          // TODO: change to absolute url
          images: [{ url: ogImage.node.publicURL }]
        }}
      />
      <Page className={campaign}>
        {contentRenderer(images.allFile.edges)(hero)}
        <Content>
          <Section images={images.allFile.edges} sections={sections} />
        </Content>
      </Page>
    </Layout>
  )
}

export default IssueTemplate
